
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  width: 100%;
}
a{ text-decoration:none;
}

.play[data-v-4fc46aa9]{
  width: 100%;
}
.bar[data-v-4fc46aa9]{
 /* position: fixed;*/
  width: 100%;
  background-color: white;
  z-index: 1;
}
.mint-navbar .mint-tab-item[data-v-4fc46aa9]{
  padding: 7px !important
}

.header{
  background-color: #f8f8f8;
  margin: 0;
  color: gray;
  font-size: 18px;
  height: 40px;
  z-index: 100 !important;
}
.smallImg{
  margin-top: 20px;
  width: 28px;
  height: 28px;
}

p{
   word-wrap:break-word; 
   margin: 5px;
}
.mint-indicator-wrapper{
	z-index: 2100 !important;
}
.mint-toast{
	z-index: 2100 !important;
}
.changeColor{
	color:blue;
}
.line{
    display: inline-block;
    width: 100%;
    border:0.5px solid #ddd;
    text-align: center;
}
.top{
	margin-bottom: 10px;
}
.commentContainer{
  margin: 10px;
  margin-bottom: 60px;
}
a.commentInput{
	  border-top:1px solid lightgray;
	  position: fixed;
	  bottom: 0px;
	  left: 0px;
	  width: 100%;
    z-index: 100 !important;
}
.showImg{
		width: 80%;
	  margin-right: 10%;
}
.imgPop{
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
    text-align: left;
    width: 90%;
    min-height: 200px;
    max-height: 400px;
   /* height: 200px;*/
    border-radius: 3px;
    margin-right: 5%;
    padding: 10px;
}
.commentCell{
		text-align: left;
		margin: 5px 0;
		padding: 5px;
		background-color: rgba(212, 212, 212, 0.24);
		border-radius: 3px;
}
.userPhoto{
			width: 25px;
			height:25px;
			border-radius: 50%;
			border:1px solid lightgray;
			float: left;
}
.name{
    	display: inline-block;
	    padding: 5px;
	    font-size: 15px;
	    color:#607D8B;
}
.imgBox{
			display: block;
    	margin-left: 30px;
}
.text{
			margin: 0 5px;
			padding-left: 25px;
			width: 80%;
}
.textImg{
    	padding: 5px;
    	font-size: 14px;
    	display: inline-block;
}
.replyBtnCell{
			float:right;
}
.replyBtn{
				font-size: 14px;
				color:#60898b;
				padding: 0 8px;
}
.replyBtn:hover{
			    cursor: pointer;
			    color:lightblue;
}
.replyPop{
				text-align: left;
		    width: 100%;
		    height: 100%;
		    overflow: scroll;
}
.reply{
				width: 90%;
				height: 230px;
				margin-right: 5%;
}
.replyInput{
        border: 1px solid lightgray;
        border-radius: 5px;
        margin: 20px 10px;
        margin-bottom: 0px;
}
.replyCell{
			margin-top: -10px;
}


