#playerContainer{
	width: 100%;
	background-color: #5d5d5d;
}
#myplayer{
	width: 100%; 
	/*position: fixed !important;*/
	margin-top: 40px;
  background-color: white;
 /* z-index:1;*/
}
.isfixed{
	position: fixed !important;
	z-index:1;
}
.block{
  background-color: white;
  z-index: 1;
}
.none{
	display: none;
}
/* 这是整个播放器的容器，宽高由JS设置 */
.player-image-container {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background: white;
}
.player-image-section {
  overflow: hidden;
  width: 100%;
  height: 100%;
  -webkit-transition: -webkit-transform 0.2s ease;
  transition: -webkit-transform 0.2s ease;
  transition: transform 0.2s ease;
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
}
.player-image {
  width: 100%;
}
/* canvas的宽高由js设置 */
.player-canvas {
  position: absolute;
  z-index: 95;
  top: 0;
  left: 0;
}
/* 进度条 */
.player-main {
  font-size: 16px;
  line-height: 1;
  position: absolute;
  z-index: 99;
  bottom: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
  height: 30px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color:#fff;
  background-color: rgba(0, 0, 0, 0.6);
}
.player-button>i {
  opacity: .5;
}
.player-button>i:hover {
  cursor: pointer;
